// const CheckoutForm = ({ amount, price, tax }) => {
const CheckoutForm = ({ tax }) => {
  const socket = useSelector((state) => state.socket);
  const dispatch = useDispatch();
  const stripe = useStripe();
  const elements = useElements();
  const [error, setError] = useState(null);
  const [card, setCard] = useState({});
  const [processing, setProcessing] = useState(false);
  const [paymentSuccess, setPaymentSuccess] = useState(null);

  const [billingDetails, setBillingDetails] = useState({
    email: "",
    name: "",
  });

  const [amount, setAmount] = useState(1000);
  const [price, setPrice] = useState(8);
  useEffect(() => {
    if (socket.payment_secret) {
      confirmPayment(socket.payment_secret);
    }
  });

  const confirmPayment = async (payment_secret) => {
    console.log(
      "before paymentSuccess",
      payment_secret,
      billingDetails.name,
      elements.getElement(CardElement)
    );
    const result = await stripe.confirmCardPayment(payment_secret, {
      payment_method: {
        card: elements.getElement(CardElement),
        billing_details: {
          name: billingDetails.name,
        },
      },
      receipt_email: billingDetails.email,
    });

    // remove the one secret of payment
    dispatch({ type: "PAYMENT", response: null });
    if (result.error) {
      console.log("result-error", result.error);
      setError(result.error);
    } else if (result.paymentIntent.status === "succeeded") {
      console.log("result-paymentSuccess", result.paymentIntent);
      console.log("email to send payment", socket.status.email);
      SocketClient.getInstance().send("paymentSuccess", {
        paymentIntent: result.paymentIntent,
        email: socket.status.email,
        tabId: 1,
      });
      setPaymentSuccess(result.paymentIntent);
    }
  };
  const handleSubmit = async (event) => {
    event.preventDefault();
    if (!stripe || !elements) {
      return;
    }
    if (error) {
      elements.getElement("card").focus();
      return;
    }
    SocketClient.getInstance().send("payment", {
      amount: amount,
      tax: tax,
      tabId: 1,
    });
    setCard(elements.getElement(CardElement));
    console.log(
      "elements.getElement(CardElement)",
      elements.getElement(CardElement)
    );
    // alert('setcard')
  };
 
  const reset = () => {
    setError(null);
    setProcessing(false);
    setPaymentSuccess(null);
    setBillingDetails({
      email: "",
      name: "",
    });
  };

   function handleChange(e) {
    console.log(e.target.value, getPricePlanByCredits(e.target.value).price)
    setAmount(e.target.value);
    setPrice(getPricePlanByCredits(e.target.value).price) 
  }
   const _pricePlans = {
    1000: { price: 8, description: "5K to 10K", dueTo: "40 minutes" },
    5000: { price: 39, description: "10K to 100K", dueTo: "40 minutes" },
    25000: { price: 159, description: "100K to 250K", dueTo: "40 minutes" },
  };
  function getPricePlanByCredits(credits) {
    for (const planIndex in _pricePlans) {
      if (planIndex == credits) {
        return _pricePlans[planIndex];
      }
    }
  }
  return paymentSuccess ? (
    <div className="Result">
      <div className="ResultTitle" role="alert">
        Payment successful. Thank you.
        <br />
        Your credit balance has been updated.
      </div>
      <div className="ResultMessage"></div>
      {/* <ResetButton onClick={reset} /> */}
    </div>
  ) : (
    <div>
      <div className="box mt-4">
        <select
          value={amount}
          onChange={handleChange}
          style={{ width: "100%" }}
        >
          <option value="1000">1,000 credits for US $8</option>
          <option value="5000">5,000 credits for US $39</option>
          <option value="25000">25,000 credits for US $159</option>
        </select>
      </div>
      <form className="Form" onSubmit={handleSubmit}>
        {/* <fieldset className="FormGroup"> */}
        <Field
          label="Name"
          id="name"
          type="text"
          placeholder="Name"
          required
          autoComplete="name"
          value={billingDetails.name}
          onChange={(e) => {
            setBillingDetails({ ...billingDetails, name: e.target.value });
          }}
        />
        <Field
          label="Email"
          id="email"
          type="email"
          placeholder="Email"
          required
          autoComplete="email"
          value={billingDetails.email}
          onChange={(e) => {
            setBillingDetails({ ...billingDetails, email: e.target.value });
          }}
        />
        <fieldset className="FormGroup">
          <CardField
            onChange={(e) => {
              setError(e.error);
            }}
          />
        </fieldset>
        {error && <ErrorMessage>{error.message}</ErrorMessage>}

        {tax ? (
          <div>
            <div className="row">
              <div className="col-sm-6">Purchase {amount} credits:</div>
              <div className="col-sm-3">${parseFloat(price).toFixed(2)}</div>
            </div>
            <div className="row">
              <div className="col-sm-6">GST(10%):</div>
              <div className="col-sm-3">
                ${(parseFloat(price) / 10).toFixed(2)}
              </div>
            </div>
            <div className="row">
              <div className="col-sm-6">TOTAL:</div>
              <div className="col-sm-3">
                ${(parseFloat(price) + parseFloat(price) / 10).toFixed(2)}
              </div>
            </div>
          </div>
        ) : (
          ""
        )}
        <div className='creditSBWrapper'>
          <SubmitButton processing={processing} error={error} disabled={!stripe}>
            Pay US $
            {tax
              ? (parseFloat(price) + parseFloat(price) / 10).toFixed(2)
              : parseFloat(price).toFixed(2)}
          </SubmitButton>
          <img src={logo} className='stripeLogo'></img>
        </div>
      </form>
    </div>
  );
};
export default CheckoutForm;
