import React, { Component, useEffect, useState } from "react";
import { connect, useDispatch, useSelector } from "react-redux";
import { bindActionCreators } from "redux";
import SocketClient from "../../utils/socketClient";
// import "./style.css";
import Validating from "../validating";

const Validate = () => {
  const dispatch = useDispatch();
  const [isDownload, setIsDownload] = useState(false);
  const [isUpdate, setIsUpdate] = useState(false);
  const [isDelete, setIsDelete] = useState(false);
  const [isDisable, setIsDisable] = useState(false);

  useEffect(() => {
    //  alert(scanedPerson);
    /**
     * if not found emails redirect scan seleted page with email not found message
     */
    if (socket.scanned_emails == 0)
      dispatch({ type: "SET_STAGE", response: "email_not_found" });
  }, []);
  const validate = () => {
    // console.log('isDownload',isDownload)
    // console.log('isDelete',isDelete)
    if (isUpdate) {
      SocketClient.getInstance().send("validate", {
        csv: socket.csv,
        email: socket.email,
        idToEmail: socket.idToEmail,
        scanned_persons: socket.scanned_persons,
        isDownload: isDownload,
        isUpdate: isUpdate,
        isDelete: isDelete,
      });
    } else {
      SocketClient.getInstance().send("validate", {
        csv: socket.csv,
        email: socket.email,
        idToEmail: socket.idToEmail,
        scanned_persons: socket.scanned_persons,
        isDownload: isDownload,
        isUpdate: false,
        isDelete: false,
      });
    }
  };
  const handleScanData = (value, type = 0) => {
    console.log("handleScanData : ----- ", value, type);
  };

  const socket = useSelector((state) => state.socket);
  return (
    <div className='mt-2 box p-2'>
      {/* <h3>Scanned</h3> */}
      <b>Bulk Email Validation - Step 2</b>
      <br/>
      {socket.error !=="" && <p style={{color:'red'}}>{socket.error}</p>}
      <br/>
      <p>We have scanned {SocketClient.getInstance().numberWithCommas(socket.scanned_persons)} contacts and found {SocketClient.getInstance().numberWithCommas(socket.scanned_emails)} unique emails</p>
      <br/>
      <div className="box row">
        {/* Default switch  */}
        <div className="col-sm-7">
          <p className="label">Download ResultFile</p>
        </div>
        <div className="col-sm-5">
          <label className="switch">
            <input
              className="switch-input"
              type="checkbox"
              checked={isDownload}
              onClick={() => setIsDownload(!isDownload)}
            />
            <span className="switch-label" data-on="On" data-off="Off"></span>
            <span className="switch-handle"></span>
          </label>
        </div>
      </div>
      <div className="box row">
        <div className="col-sm-7">
          <p className="label">Update Pipedrive</p>
        </div>

        <div className="col-sm-5">
          <label className="switch">
            <input
              className="switch-input"
              type="checkbox"
              checked={isUpdate}
              onClick={() => setIsUpdate(!isUpdate)}
            />
            <span className="switch-label" data-on="On" data-off="Off"></span>
            <span className="switch-handle"></span>
          </label>
        </div>
      </div>
      <div className="box row">
        <div className="col-sm-7">
          <p className="label ml-3">Append invalid addresses</p>
        </div>

        <div className="col-sm-5">
          <input
            type="radio"
            name="pipRadio"
            checked={!isDelete}
            onClick={() => setIsDelete(false)}
            disabled={isUpdate ? false : true}
          />
        </div>
      </div>
      <div className="box row">
        <div className="col-sm-7">
          <p className="label ml-3">Delete invalid addresses</p>
        </div>

        <div className="col-sm-5">
          <input
            type="radio"
            name="pipRadio"
            checked={isDelete}
            onClick={() => setIsDelete(true)}
            disabled={isUpdate ? false : true}
          />
        </div>
      </div>

      <div className="box mt-4"></div>
      <div className="box mt-4">
        {/* <button onClick={scanSelected}><i className="fa fa-search mr-3"></i>Scan Selected</button> */}
        <button className="button ml-3" onClick={validate} disabled={(!isDownload && !isUpdate)? true:false}>
          Go!
        </button>
      </div>
    </div>
  );
};

export default Validate;
