import React, { Component, useEffect, useState } from "react";
import { connect, useDispatch, useSelector } from "react-redux";
import { bindActionCreators } from "redux";
import SocketClient from "../../utils/socketClient";
// import "./style.css";
import Scanning from "../scanning";
import ScanSelect from "../scan";
import Validating from "../validating";
const Config = require("../../utils/config");

const ScanSelected = ({ props }) => {
  const dispatch = useDispatch();
  const [filters, setFilters] = useState([]);
  // const [status, setStatus] = useState('');
  const [idOfFilter, setIdOfFilter] = useState(0);
  useEffect(() => {
    /** Get Filters from Pipedrive */
    if (socket.loggedIn) {
      fetch(
        `https://api.pipedrive.com/v1/filters?type=people`,
        {
          method: "GET",
          mode: "cors",
          headers: {"Authorization":"Bearer "+localStorage.getItem("access_token").toString().replace(/"/g, "")}
        }
      )
        .then((r) => r.json())
        .then((data) => {
          console.log("get filters with access_token", data.data)
          setFilters(data.data);
          setFilters((state) => [{ id: 0, name: "All Contacts" }, ...state]);
          // collectEmails(data.data);
        });
        SocketClient.getInstance().send("status", { token: Config.CONFIG.PIPEDRIVE_API_TOKEN, id:socket.user.id,  socket_id:localStorage.getItem('socket_id'), refresh_token:localStorage.getItem('refresh_token')});
    }
  }, []);

  const socket = useSelector((state) => state.socket);

  function handleChange(e) {
    setIdOfFilter(e.target.value);
  }
  let scanStatus = {
    message: "starting scaning",
    scanned: 0,
    emails: 0,
    invalid_emails: 0,
  };

  const scanSelected = () => {
    /**Click Scan Selected Button, Current Stage is scanning */
    dispatch({ type: "SET_STAGE", response: "scanning" });
    // state is scanning emails
  };
  let filterList=""
  if(filters.length){
    filterList =
    filters.length > 0 &&
    filters.map((item, i) => {
      return (
        <option key={i} value={item.id}>
          {item.name}
        </option>
      );
    }, this);
  }
  return (
    <div className='divSection'>
      {/* {socket.error !=="" && <p style={{color:'red'}}>{socket.error}</p>} */}
      
      {/* Redirect Scanning page with Filter ID when button clicked(stage is scanning)  */}
      {socket.stage === "scanning" && <Scanning idOfFilter={idOfFilter} />}

      {/* Redirect Scanning page with Filter ID when button clicked(stage is scanning)  */}
      {socket.stage === "validating" && <Validating />}

      {/* Redirect Scanning page with Filter ID when button clicked(stage is scanning)  */}
      {socket.stage === "finished" && <Validating />}

      {/*In Scanning, redirect to Validate after scan_finished. */}
      {socket.stage === "scan_finished" && <Scanning />}

      {/* current page, stage  */}
      {socket.stage === "selected" && (
        <div className='mt-2 box p-2'>
          <b>Bulk Email Validation - Step 1</b>
          <br/>
          {socket.error !=="" && <p style={{color:'red'}}>{socket.error}</p>}
          <br/>
          <div>
          Choose "All Contacts" or a Pipedrive filter. We will then scan the list for duplicates.
          </div>
          <div className="box mt-4">
            <select className="mr-1" value={idOfFilter} onChange={handleChange}>
              {filterList}
            </select>
            <button className="button ml-1" onClick={scanSelected}>Go!</button>
          </div>
        </div>
      )}

      {/* not email found show the error(red) messge */}
      {socket.stage === "email_not_found" && (
        <div className='mt-2 box p-2'>
          <b>Bulk Email Validation - Step 1</b>
          <div className="mt-4" style={{ color: "red" }}>
            Emails Not Found with Seleted Filter. Try again.
          </div>
          <div>
          Choose "All Contacts" or a Pipedrive filter. We will then scan the list for duplicates.
          </div>
          <div className="box">
            <select className="mt-2" value={idOfFilter} onChange={handleChange}>
              {filterList}
            </select>
          </div>

          <div className="mt-4">
            Contacts might have than one email or duplicates. Before we can
            continue we need to find all of them. This usually takes less than a
            minute.
          </div>

          <div className="box">
            <button className="button" onClick={scanSelected}>Go!</button>
          </div>
        </div>
      )}
    </div>
  );
};
export default ScanSelected;
